import FreeLesson from "../components/modal/freeLesson/FreeLesson";
class TechnicalFunctions {

    formValidate(className,emailName) {
        let error = 0;
        let formReq = document.querySelectorAll(`.${className}`);
        for(let i = 0; i< formReq.length; i++) {
            let input = formReq[i];
            this.formRemoveError(input);

            if(input.classList.contains(emailName)) {
                 if(!this.emailTest(input)){
                    this.formAddError(input);
                    error++;
                 };
            } else {
                if(input.value == '') {
                    this.formAddError(input);
                    error++;
                };
            };
        };
        return error;
    };

    formAddError(input) {
        input.classList.add('_error');
        if(input.placeholder == 'example@mail.ru' || input.placeholder == '–í–≤–µ–¥–∏—Ç–µ –ø–æ—á—Ç–æ–≤—ã–π –∞–¥—Ä–µ—Å') {
            input.placeholder = '–í–≤–µ–¥–∏—Ç–µ –ø–æ—á—Ç–æ–≤—ã–π –∞–¥—Ä–µ—Å';
        } 
    }
    
    formRemoveError(input) {
        input.classList.remove('_error');
    }
    emailTest(input) {
        return /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/.test(input.value);
    }

    addListenerForDataInput() {
        document.addEventListener('click', function(event) {
            let target = event.target;
            if(!target.hasAttribute('data-input')) return;
            target.classList.remove('_error');
        });
    };

    addListenerDataClose() {
        document.addEventListener('click', function(event) {
            let target = event.target;
            if(target.dataset.close) {
                FreeLesson.close();
            };
        });
    };

    addListenerForFreeLesson() {
        let record = document.getElementById('full-screen-record');
        record.addEventListener('click', function() { // –∑–∞–ø–∏—Å—å –Ω–∞ –±–µ—Å–ø–ª–∞—Ç–Ω–æ–µ –∑–∞–Ω—è—Ç–∏–µ —á–µ—Ä–µ–∑ –≥–ª–∞–≤–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
            FreeLesson.open(); // –æ—Ç–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
            FreeLesson.sendForm();
            document.addEventListener('keydown', function(event) {
                if(event.code.toLowerCase() == 'escape') {
                    FreeLesson.close();
                };
            });
            let inputTel = document.querySelector('.modal__input_phone');

            inputTel.addEventListener('keyup', function(event) {
                if(event.key != 'Backspace' && (inputTel.value.length === 1 || inputTel.value.length === 5 || inputTel.value.length === 9 || inputTel.value.length === 12)) {
                    inputTel.value += '-'
                }
            })
        });

    };
};

export default new TechnicalFunctions()